import { PreventableEvent } from './preventable-event';

/**
 * Arguments for the `legendItemClick` event.
 */
export class LegendItemClickEvent extends PreventableEvent {
  /**
   * An object containing the series configuration.
   *
   * > The interface of this property is unstable
   * and might change for the 1.0 release.
   */
  public series: any;

  /**
   * The index of the series in the parent Chart.
   */
  public seriesIndex: number;

  /**
   * The point index in the series.
   *
   * Applicable only to the Pie, Donut, and
   * Funnel series.
   */
  public pointIndex: number;

  /**
   * The text of the legend item.
   */
  public text: string;

  /**
   * Constructs the event arguments from a raw object.
   */
  constructor(e: any) {
    super();

    this.series = e.series;
    this.seriesIndex = e.seriesIndex;
    this.pointIndex = e.pointIndex;
    this.text = e.text;
  }

  /**
   * If called, the series visibility is not toggled
   * as a result of clicking the legend item.
   */
  public preventDefault(): void {
    super.preventDefault();
  }
}



